<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12 sm6 class="text-xs-center">
          <img src="static/img/icons/namelogo-192x192.png" width="120px"/>
          <br/><br/>

          <p>
            Read Bhagvath Gita <br>
            in language of your choice on any device.
          </p>
      </v-flex>
      <v-flex xs12 class="text-xs-center" v-for="lang in languages" :key="lang.code" v-if="lang.code!='sa'">
        <v-btn dark block round color="error" @click="goto(lang.code)">{{lang.name}}</v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
import {HTTP} from '@/common/http-common'
export default {
  data () {
    return {
      languages: [],
      errors: []
    }
  },

  mounted () {
    HTTP.get('languages').then(response => {
      this.languages = response.data
    }).catch(e => {
      this.errors.push(e)
    })
  },

  methods: {
    goto (lang) {
      this.$store.dispatch('loadChanges', {lang: lang})
      var verseId = this.$store.getters.getCurrentVerseId
      this.$router.push({
        path: `Verses`,
        query: { scriptureId: this.$store.getters.getScriptureId, previousVerseId: verseId, lang: lang }
      })
    }
  }
}
</script>

<style scoped>
  p {
    color: gray;
    font-family: Helvetica, Arial, sans-serif;
    font-size: 20px;
  }
</style>





